import hex_sha1 from "sha1";
import noData from '../../components/noData/noData.vue'
import uniLoadMore from "@/components/uni-load-more.vue";
import Loading13 from "@/components/loading/loading13.vue";
import axios from "axios";
import wx from 'weixin-js-sdk'
import {
	ChangePageTitle
} from '../../title.js'
export default {
	data() {
		return {
			back1: true,
			title: '客户名称',
			// accessToken: '64e9275246c6545a3b5d3ad060fe4c516f44d556',
			// appsercert: 'MTExMTEx',
			accessToken: '',
			appsercert: '',
			page: 1,
			commonList: [],
			impList: [],
			status: 'more',
			isShow: false,
			dis: false,
			isShow_loading: false,
			timeOutEvent: 0,
			ss: '',
			id: '',
			show: false,
			showList: false,
			showList2: false,
			isdown: '',
			dataImg:false
		}
	},
	components: {
		uniLoadMore,
		Loading13,
		noData
	},
	onLoad(e) {
		var ua = navigator.userAgent.toLowerCase();
		var that = this;
		if (ua.indexOf('iphone') != -1 || ua.indexOf('android') != -1) {
			this.isShow_loading = true
			var list = uni.getStorageSync('login');
			// alert(JSON.stringify(list))
			if (list.access_token) {
				uni.request({
					url: 'https://testapi.shiliucrm.com/v1/hwechat_callback/callbacktask/authorize',
					data: {
						access_token: list.access_token
					},
					success: (res) => {
						// alert(JSON.stringify(res))
						if (res.data == 1) {
							this.accessToken = list.access_token
							this.appsercert = list.appsercert
							this.getlist()
						} else {
							window.location.href =
								'https://open.weixin.qq.com/connect/oauth2/authorize?appid=ww05249b75c1172723&redirect_uri=http://hdtest.handone.com/h5/%23/pages/login/index&response_type=code&scope=snsapi_privateinfo#wechat_redirect'
						}
					},
					fail: (err) => {
						// console.log(err)
						alert(err)
						that.dataImg = true;
					}
				})
			} else {
				window.location.href =
					'https://open.weixin.qq.com/connect/oauth2/authorize?appid=ww05249b75c1172723&redirect_uri=http://hdtest.handone.com/h5/%23/pages/login/index&response_type=code&scope=snsapi_privateinfo#wechat_redirect'
			}
			// // var list2 = JSON.parse(list)
			// if(!list){
			// 	
			// }else{
			// 	this.accessToken = list2.access_token
			// 	this.appsercert = list2.appsercert
			// 	this.isShow_loading = true
			// 	this.getlist()
			// }
		} else if (ua.indexOf('windows') != -1 || ua.indexOf('mac') != -1) {
			window.location.href =
				'https://open.weixin.qq.com/connect/oauth2/authorize?appid=ww05249b75c1172723&redirect_uri=http://hdtest.handone.com/site/qylogin&response_type=code&scope=snsapi_userinfo#wechat_redirect  '
		}
	},
	methods: {
		gogunlain() {
			uni.navigateTo({
				url: '/pages/relehand/index'
			})
		},
		getlist() {
			let _this = this
			var signStr =
				"access-token=" +
				_this.accessToken +
				"&appsercert=" +
				_this.appsercert +
				"&page=" +
				_this.page;
			// console.log(signStr)
			_this.sha = hex_sha1(signStr);
			// _this.dataImg = true;
			console.log(_this.sha)
			axios.get(_this.$baseUrl.tasklist.url + '?access-token=' + _this.accessToken + '&sign=' + _this.sha)
				.then((res) => {
					console.log(res)
					_this.isShow_loading = false
					if (res == undefined) {
						uni.showToast({
							title: '网络异常',
							icon: 'none'
						})
						_this.dataImg = true;
					} else {
						_this.commonList = res.data;
						if(_this.commonList.length == 0) {
							_this.dataImg = true;
						} else {
							_this.dis = true;
							// _this.dataImg = false;
							for (var i = 0; i < res.data.length; i++) {
								if (res.data[i].t_tag == 1) {
									_this.impList = _this.impList.concat(res.data[i])
								}
							}
						}
					}
				})
				.catch((err) => {
					console.log(err)
					_this.dataImg = true
				})
		},
		//删除项目
		delItem(id) {
			console.log(this.timeOutEvent)
			this.ss = setInterval(() => {
				this.timeOutEvent++
				console.log(this.timeOutEvent)
				if (this.timeOutEvent >= 1) { //点击
					//此处为点击事件----在此处添加跳转详情页
					console.log('长按了')
					console.log(id)
					this.id = id
					this.show = true
					this.showList = true
					clearInterval(this.ss)
					this.timeOutEvent = 0
				}
			}, 1000)
		},
		touchmove: function(e) {
			// console.log(e)
			// this.timeOutEvent = 0;
			// e.preventDefault();
		},
		touchend: function(e) {
			// console.log(this.timeOutEvent);
			clearTimeout(this.timeOutEvent);
			if (this.timeOutEvent >= 1) { //点击
				//此处为点击事件----在此处添加跳转详情页
				clearInterval(this.ss)
				this.timeOutEvent = 0
			} else {
				clearInterval(this.ss)
				this.timeOutEvent = 0
			}
		},
		down() {
			this.isdown = true
		},
		//删除项目交互
		//取消
		leave_list() {
			this.show = false
			this.showList = false
			this.showList2 = false
		},
		//删除项目，弹出确认删除
		defineDel() {
			if (this.isdown) {
				this.showList = false
				this.showList2 = true
				this.isdown = false
			}
		},
		//是否删除此项目取消
		cancel() {
			this.leave_list()
		},
		//确定删除项目
		del_plate() {
			var _this = this
			var signStr =
				"access-token=" +
				_this.accessToken +
				"&appsercert=" +
				_this.appsercert +
				'&id=' +
				_this.id
			// console.log(signStr)
			_this.sha = hex_sha1(signStr);
			console.log(_this.sha)
			_this.$axios({
					url: this.$baseUrl.del_item.url + _this.id + '?access-token=' + _this.accessToken +
						'&sign=' + _this
						.sha,
					method: 'delete'
				})
				.then(res => {
					console.log(res)
					if (res.data.msg == '成功') {
						uni.showToast({
							title: '删除成功',
							icon: 'none'
						})
						_this.show = false
						_this.showList2 = false
						_this.dis = false
						_this.$nextTick(function() {
							_this.commonList = []
							_this.impList = []

							_this.getlist()
						})
					}
				}, (error) => {
					console.log(error)
				})
		},
		//跳转详情
		godetail(id) {
			uni.navigateTo({
				url: '/pages/itemdetail2/index?id=' + id
			})
		},
		//设置为常用项目
		setImp(id, i) {
			console.log(i)
			if (i == 0) {
				this.$axios({
						url: this.$baseUrl.isImportant.url + '?access-token=' + this.accessToken,
						method: 'post'
					}, {
						t_id: id,
						type: 1
					})
					.then(res => {
						console.log(res)
						this.dis = false
						this.commonList = []
						this.impList = []
						this.getlist()
					}, (error) => {
						console.log(error)
					})
			} else if (i == 1) {
				this.$axios({
						url: this.$baseUrl.isImportant.url + '?access-token=' + this.accessToken,
						method: 'post'
					}, {
						t_id: id,
						type: 0
					})
					.then(res => {
						console.log(res)
						this.dis = false
						this.commonList = []
						this.impList = []
						this.getlist()
					}, (error) => {
						console.log(error)
					})
			}

		}
	},
	mounted() {},
	onShow() {
		document.title = 'handone'
		ChangePageTitle('handone')
	}

}
